import { ChangeDetectionStrategy, Component, Input, OnInit, } from '@angular/core';
import { FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
//
import { TranslateService } from '@ngx-translate/core';
//
import { Brand } from '../../../ms-brands/models/brand';
import { Category } from '../../../ms-categories/models/category';
import { Collection } from '../../../ms-collections/models/collection';
import { Release } from '../../../ms-releases/models/releases';
import { COLORS, Color } from '../../../ms-releases/models/color';
import { GENDERS, Gender } from '../../../ms-releases/models/gender';
import { Shop } from '../../../ms-shops/models/shops';
import { Style } from '../../../ms-style/models/style';
import { LayoutSlider } from '../../models/layout';
import { FilterItem } from '../../models/filters';

//
import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';

@Component({
    selector: 'filter-header-form',
    templateUrl: './filter-header-form.component.html',
    styleUrls: ['./filter-header-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class FilterHeaderFormComponent extends BaseReactiveFormComponent<Array<FilterItem>> implements OnInit {

    genders: Gender[] = GENDERS;

    colors: Color[] = COLORS;

    filtersArray: Array<FilterItem> = [];

    @Input() filters: any;

    @Input() brands: Array<Brand>;

    @Input() categories: Array<Category>;

    @Input() collections: Array<Collection>;

    @Input() releases: Array<Release>;

    @Input() shops: Array<Shop>;

    @Input() styles: Array<Style>;

    constructor(translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);
    }

    ngOnInit() {

        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.createFormGroup();
    }

    createFormGroup() {

        this.formGroup = new FormGroup({
            style: new FormControl({ value: this.getFilterValue('style'), disabled: this.getFilterActive('style') }),
            sku: new FormControl({ value: this.getFilterValue('sku'), disabled: this.getFilterActive('sku') }),
            brand: new FormControl({ value: this.getFilterValue('brand'), disabled: this.getFilterActive('brand') }),
            collection: new FormControl({ value: this.getFilterValue('collection'), disabled: this.getFilterActive('collection') }),
            category: new FormControl({ value: this.getFilterValue('category'), disabled: this.getFilterActive('category') }),
            gender: new FormControl({ value: this.getFilterValue('gender'), disabled: this.getFilterActive('gender') }),
            color: new FormControl({ value: this.getFilterValue('color'), disabled: this.getFilterActive('color') }),
            shop: new FormControl({ value: this.getFilterValue('shop'), disabled: this.getFilterActive('shop') }),
            shipping: new FormControl({ value: this.getFilterValue('shipping'), disabled: this.getFilterActive('shipping') }),
            status: new FormControl({ value: this.getFilterValue('status'), disabled: this.getFilterActive('status') }),
            maxPrice: new FormControl({ value: this.getFilterValue('maxPrice'), disabled: this.getFilterActive('maxPrice') }),
            minPrice: new FormControl({ value: this.getFilterValue('minPrice'), disabled: this.getFilterActive('minPrice') }),
        });

    }

    submitClicked() {
        if (this.formGroup.valid) {
            this.accept.emit(this.filters);
        } else {
            this.triggerValidation();
        }
    }

    addFilter(filter: string) {
        if (!this.filters) {
            this.formGroup.get(filter).enable();
            return;
        }
        if (this.filters.find(item => { return item.filter === filter })) {
            this.filters = this.filters.filter(item => {
                return item.filter !== filter;
            });
            this.formGroup.get(filter).setValue('');
            this.formGroup.get(filter).disable();
        } else {
            this.formGroup.get(filter).enable();

        }
    }

    updateFilterValue(filter: string, name: string) {
        if (!this.filters) {
            this.filters = [];
        }
        if (!this.filters.find(item => { return item.filter === filter })) {
            let filterItem = new FilterItem();
            filterItem = {
                filter: filter,
                name: name,
                value: this.formGroup.get(filter).value,
            }
            this.filters = [...this.filters, filterItem];
        }
        else {
            try {
                this.filters.find(item => {
                    return item.filter === filter
                }).value = this.formGroup.get(filter).value;
                this.filters.find(item => {
                    return item.filter === filter
                }).name = name;
            }
            catch (err) {
            }
        }

    }

    getFilterValue(filter: string) {
        if (this.filters) {
            if (this.filters.find(item => { return item.filter === filter })) {
                return this.filters.find(item => { return item.filter === filter }).value;
            };
        }
        return '';
    }

    getFilterActive(filter: string) {
        if (this.filters) {
            if (this.filters.find(item => { return item.filter === filter })) {
               return false;
            };
        }
        return true;
    }
}

