import { Component, AfterViewInit, Inject, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { ValidationErrors } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
//
import { forkJoin } from 'rxjs';
import { Observable } from 'rxjs/Observable';
import { MatDialog, MAT_DIALOG_DATA, MatDialogRef, MatSnackBar } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
//
import { Brand } from '../../../ms-brands/models/brand';
import { Category } from '../../../ms-categories/models/category';
import { Collection } from '../../../ms-collections/models/collection';
import { Release } from '../../../ms-releases/models/releases';
import { Shop } from '../../../ms-shops/models/shops';
import { Style } from '../../../ms-style/models/style';
//
import { Face, State, Status } from '../../../../../ui/modules/images-card/models/face';
import { ImagesService } from '../../../../../ui/modules/images-card/services/images.service';
import { CanDeactivateMixin } from '../../../../../ui/helpers/component-can-deactivate';
import { ConfirmDialogComponent } from '../../../../../ui/modules/confirm-dialog/components/confirm-dialog/confirm-dialog.component';
import { Mixin } from '../../../../../ui/helpers/mixin-decorator';
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
import { ToastrService } from '../../../../../error-handling/services/toastr.service';
import { FilterItem, FilterResponce } from '../../models/filters';
import { LayoutSlider, LayoutHeader } from '../../models/layout';
import { LayoutService } from '../../services/layout.service';
//import { setTranslations } from '@c/ngx-translate';

const errorKey = 'Error';

const updatedMessageKey = 'Updated';

@Component({
  selector: 'new-task',
  templateUrl: './new-task.component.html',
  styleUrls: ['./new-task.component.scss']
})

@Mixin([CanDeactivateMixin])
export class NewTaskComponent implements AfterViewInit, CanDeactivateMixin {

  data = new LayoutHeader();

  validationErrors: ValidationErrors;

  Urls: Array<LayoutSlider>;

  // Begin Mixin code of the CanDeactivate class
  unsavedChanges = false;

  cancelBtnKey = 'No';

  okBtnKey = 'Yes';

  saveTitleKey = 'Discard Title';

  saveMessageKey = 'Discard Message';

  modalRef: MatDialogRef<ConfirmDialogComponent>;

  canDeactivate: () => Observable<boolean> | boolean;

  dataChanged: () => void;

  urlId: string;

  @Input() filters: any;

  @Input() pageId: string;

  @Input() brands: Array<Brand>;

  @Input() categories: Array<Category>;

  @Input() collections: Array<Collection>;

  @Input() releases: Array<Release>;

  @Input() shops: Array<Shop>;

  @Input() styles: Array<Style>;

  constructor(
    public activatedRoute: ActivatedRoute,
    public dialog: MatDialog,
    private imagesService: ImagesService,
    public layoutService: LayoutService,
    private errorHandlingService: ErrorHandlingService,
    public router: Router,
    public snackBar: MatSnackBar,
    private translate: TranslateService,
    private toastr: ToastrService,
    public dialogRef: MatDialogRef<NewTaskComponent>,
    @Inject(MAT_DIALOG_DATA) public dialogData: any
  ) {
    //setTranslations(this.translate, TRANSLATIONS);
  }

  ngOnInit() {
    this.pageId = this.dialogData.pageId;
    this.data = {
      'columns': {
        'url': 'string',
        'filter': [],
        'filters': [],
      }
    }
  }

  ngAfterViewInit() {
    //this.getUrl();
  }

  getLayoutSliders(filters: Array<any>) {
    this.layoutService.getLayoutFilterSliders(this.pageId, 'header', filters).subscribe(response => {
      //this.data = response.data;      
    },
      (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error)
    )
  }

  submit(data: any) {
    //console.log(JSON.stringify(data));
    data.columns.filter = this.getFilterArray(data.columns.filters, data.filter);
    delete data.id;
    data.columns = [data.columns];

    if (data.columns.filter) {
      this.updateHeader(data);
    }
  }

  cancel() {
    this.close();
  }

  close() {
  }

  updateSlider(data: LayoutHeader) {
    this.layoutService.putUrl(this.pageId, 'header', data).subscribe(response => {
      this.unsavedChanges = false;
      this.close();
      this.toastr.success(updatedMessageKey);
    },
      (error: HandledError) => {
        this.errorHandlingService.handleUiError(errorKey, error);
        this.validationErrors = error.formErrors;
      });
  }

  editCase(headerData: LayoutHeader) {
    let mainImageFlag: boolean = false;
    if (headerData !== undefined && headerData !== null) {
      // Saving the images of the case before saving the case      
      headerData.images = [];
      const imagesObservables = new Array<Observable<any>>();
      const addedFaces = new Array<Face>();
      // Uploading the images to command and control server
      for (const position in headerData.faces) {
        const face = headerData.faces[position];
        if (face.state === State.New) {
          face.status = Status.Uploading;
          const subscription$ = this.imagesService.postImage(face.file);
          imagesObservables.push(subscription$);

          addedFaces.push(face);
        } else {
          headerData.images = [...headerData.images, face];
        }
      }
      if (imagesObservables.length > 0) {

        forkJoin(imagesObservables).subscribe(responses => {
          for (const item in responses) {
            let image: any = {
              url: '',
              display: 'small',
            };
            image.url = responses[item].data.url;
            headerData.images = [...headerData.images, image];
          }
          this.layoutService.putUrl(this.pageId, 'header', headerData).subscribe(response => {
            this.unsavedChanges = false;
            this.close();
            this.toastr.success(updatedMessageKey);
          },
            (error: HandledError) => {
              this.errorHandlingService.handleUiError(errorKey, error);
              this.validationErrors = error.formErrors;
            });
        },
          (error: HandledError) => {
            this.errorHandlingService.handleUiError(errorKey, error);
            this.validationErrors = error.formErrors;
          });
      } else {
        this.layoutService.putUrl(this.pageId, 'header', headerData).subscribe(response => {
          this.unsavedChanges = false;
          this.close();
          this.toastr.success(updatedMessageKey);
        },
          (error: HandledError) => {
            this.errorHandlingService.handleUiError(errorKey, error);
            this.validationErrors = error.formErrors;
          });
      }
    }
  }

  updateHeader(headerData: Brand) {
    if (headerData.faces) {
      for (const position in headerData.faces) {
        const face = headerData.faces[position];
        if (face.state === State.New) {
          const subscription$ = this.imagesService.postImage(face.file).subscribe(response => {
            headerData.imgUrl = response.data.url;
            this.layoutService.putUrl(this.pageId, 'header', headerData).subscribe(response => {
              this.unsavedChanges = false;
              this.close();
              this.toastr.success(updatedMessageKey);
            },
              (error: HandledError) => {
                this.errorHandlingService.handleUiError(errorKey, error);
                this.validationErrors = error.formErrors;
              });
          },
            (error: HandledError) => {
              this.errorHandlingService.handleUiError(errorKey, error);
              this.validationErrors = error.formErrors;
            });
        } else {
          this.layoutService.putUrl(this.pageId, 'header', headerData).subscribe(response => {
            this.unsavedChanges = false;
            this.close();
            this.toastr.success(updatedMessageKey);
          },
            (error: HandledError) => {
              this.errorHandlingService.handleUiError(errorKey, error);
              this.validationErrors = error.formErrors;
            });
        }
      }
    } else {
      this.layoutService.putUrl(this.pageId, 'header', headerData).subscribe(response => {
        this.unsavedChanges = false;
        this.close();
        this.toastr.success(updatedMessageKey);
      },
        (error: HandledError) => {
          this.errorHandlingService.handleUiError(errorKey, error);
          this.validationErrors = error.formErrors;
        });
    }
  }

  getFilterArray(filters: Array<any>, filter: any) {

    let queryParams: any = {};

    if (filters) {
      filters.forEach(item => {
        let filter = item.filter;
        queryParams[item.filter] = item.value;
      })
      console.log(JSON.stringify(queryParams));
    }
    return queryParams;
  }
}
