import { ChangeDetectionStrategy, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
//
import { MatDialog, MatDialogRef } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
//
import { Brand } from '../../../ms-brands/models/brand';
import { Category } from '../../../ms-categories/models/category';
import { Collection } from '../../../ms-collections/models/collection';
import { Release } from '../../../ms-releases/models/releases';
import { Shop } from '../../../ms-shops/models/shops';
import { Style } from '../../../ms-style/models/style';

import { Face, State, Status } from '../../../../../ui/modules/images-card/models/face';

import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';

import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';
import { LayoutSlider } from '../../models/layout';
import { NewFilterHeaderComponent } from '../new-filter-header/new-filter-header.component';
import { LayoutService } from '../../services/layout.service';

const errorKey = 'Error';

@Component({
    selector: 'task-form',
    templateUrl: './task-form.component.html',
    styleUrls: ['./task-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class HeaderFormComponent extends BaseReactiveFormComponent<LayoutSlider> implements OnInit {

    displayedColumns: string[] = [
        'URL',
        'VANITY URL',
        'ACTIONS'
    ];

    modalRef: MatDialogRef<NewFilterHeaderComponent>;

    faces: FormControl;

    @Input() filters: any = [];

    @Input() brands: Array<Brand>;

    @Input() categories: Array<Category>;

    @Input() collections: Array<Collection>;

    @Input() pageId: string;

    @Input() releases: Array<Release>;

    @Input() shops: Array<Shop>;

    @Input() styles: Array<Style>;

    @ViewChild('url') fileInput: ElementRef;

    imageList: Array<Face>;

    filtersArray: Array<any>;

    constructor(
        public dialog: MatDialog,
        private eleRef: ElementRef,
        public errorHandlingService: ErrorHandlingService,
        private formBuilder: FormBuilder,
        public layoutService: LayoutService,
        public translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);
    }

    ngOnInit() {
        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.createFormGroup();
    }

    createFormGroup() {
        this.faces = this.formBuilder.control(this.data.faces);

        this.formGroup = new FormGroup({
            faces: this.faces,
            numberOfItem: new FormControl(this.data.numberOfItem),
            url: new FormControl(this.data.url),
            filtersForm: this.formBuilder.array([
                this.formBuilder.group({
                    style: new FormControl(this.getFilterValue('style')),
                    sku: new FormControl(this.getFilterValue('sku')),
                    brand: new FormControl(this.getFilterValue('brand')),
                    collection: new FormControl(this.getFilterValue('collection')),
                    category: new FormControl(this.getFilterValue('category')),
                    gender: new FormControl(this.getFilterValue('gender')),
                    color: new FormControl(this.getFilterValue('color')),
                    shop: new FormControl(this.getFilterValue('shop')),
                    shipping: new FormControl(this.getFilterValue('shipping')),
                    status: new FormControl(this.getFilterValue('status')),
                    maxPrice: new FormControl(this.getFilterValue('maxPrice')),
                    minPrice: new FormControl(this.getFilterValue('minPrice')),
                }) 
            ])          
        });
    }

    getLayoutSliders(filters: Array<any>) {
        this.layoutService.getLayoutFilterSliders(this.pageId, 'header', filters).subscribe(response => {
            this.data = response.data;
            this.data.faces = this.data.images;
            this.createFormGroup();
            this.fileInput.nativeElement.click()
        },
            (error: HandledError) => {
                this.data.images = [];
                this.data.faces = [];
                this.createFormGroup();
                this.fileInput.nativeElement.click();
            }//this.errorHandlingService.handleUiError(errorKey, error)
        )
    }

    submitClicked() {
        if (this.formGroup.valid) {
            this.data.filters = this.formGroup.get('filtersForm').value;
            this.data.faces = this.formGroup.get('faces').value;
            this.data.numberOfItem = this.formGroup.get('numberOfItem').value;
            this.data.url = this.formGroup.get('url').value;
            this.accept.emit(this.data);
        } else {
            this.triggerValidation();
        }
    }

    addFilterModalBrand() {
        this.modalRef = this.dialog.open(NewFilterHeaderComponent, {
            height: '80%',
            data: {
                filters: this.filters,
                brands: this.brands,
                categories: this.categories,
                collections: this.collections,
                shops: this.shops,
                styles: this.styles,
            }
        });

        this.modalRef.afterClosed().subscribe((result) => {
            this.filters = result;
            this.data.filtersActive = result;
            this.filtersArray = result;
            //this.getLayoutSliders(this.data.filters);
        });
    }

    getFilterValue(filter: string) {
        if (this.filters) {
            if (this.filters.find(item => { return item.filter === filter })) {
                return this.filters.find(item => { return item.filter === filter }).value;
            };
        }
        return '';
    }

    getFilterActive(filter: string) {

        console.log(filter);

        if (this.filters) {
            if (this.filters.find(item => { return item === filter })) {
                return false;
            };
        }
        return true;
    }

}

